.no-webp .inicio {
    background-image: url(../img/header.jpg);
}
.webp .inicio {
    background-image: url(../img/header.webp);
}


header {
    background-color: $grisOscuro;
    padding: 1rem 0 3rem 0;

    &.inicio {
        background-position: center center;
        background-size: cover;

        @include tablet {
            min-height: 75rem;
        }

        .contenido-header {
            @include tablet {
                min-height: 70rem;
            }
        }
    }
}

.contenido-header {
    display: flex;
    flex-direction: column;
    justify-content: space-between;
    
    h1 {
        text-align: left;
        color: $blanco;
        font-weight: $bold;
    }

    @include tablet {
        h1 span {
            display: block; /* Hace que el texto después del ":" pase a la siguiente línea */
        }
    }
}